@media(max-width: 576px) {
    :root {
		--fz: 12px !important;
	}


	.header {

		&-right{
			.fas{
				display: block;
				font-size: 24px;
				color: #fff;
			}
			.header__num{
				display: none;
			}
		}
		
		.fas{
			margin-right: 15px;
		}
		&-container {
			padding: 0 50px 0 20px !important;
		}
		.menu{
			width: 100px;
			height: 50px;
		}
	}
	.container{
		// padding: 20px;
	}
	.section-1{
		&-container {
			padding-left: 20px;
			
		}
	}
	.aside-btn{
		height: 50px !important;
	}
	.section-2{
		&__content-1 {
			padding: 150px 50px 150px 20px !important;

			&__title {
				margin-bottom: 20px !important;
			}
			&__descr{
				margin-bottom: 15px !important;
			}
		}
		.circle-right{
			left: -175px;

		}
		.slick-dots{
			left: 20px;
		}
	}
	.section-3{
		&-container {
			padding: 100px 20px !important;
		}
	}
	.section-4{
		&__wrapper {

			&-left{
				padding: 0 20px !important;
			}
		}
		&-title {
			font-size: 20px;
		}
		&-descr{
			font-size: 15px;
		}
		.swiper-button-prev,
		.swiper-button-next{
			width: 40px;
			height: 40px;
		}
	}
	.section-5{

		&-container {
			padding: 0 20px !important;
		}
	}
	.section-6{
		&-container {
			padding: 50px 20px !important;
		}
		.footer__copyright{
			padding: 20px 0px !important;
			text-align: center;
			p,a{
				font-size: 11px;
			}
		}
	}
}


